.Card__main {
  margin-top: .6rem;
  background: var(--bgcolor-b2);
  border-radius: .4rem;
}

.Card__title {
  padding-top: 1rem;
  display: flex;
  flex: 1;
  justify-content: left;
  align-items: center;
  margin: 0 1rem; 
}

.Card__title_text {
  margin-left: 1.4rem;
  color: rgb(180, 203, 223);
}
.Card__title_text > a {
  color: rgb(202, 114, 224);
  text-decoration: none;
}

.Card__title_text, .Card__title_text > a {
  font-size: 1.3rem;
}

/* La imagen, será un candado, o el síbolo del repositorio: */
.Card__title > img {
  width: 46px;
  background: rgb(44, 43, 43);
  margin: .4rem;
  padding: .8rem;
  border-radius: 6px;
  margin-left: 0;
}

/* La zona donde aparece el texto de la descripcion, pero no es la propiedad de text*/
.Card__description {
  margin: 0 1rem;

  background: var(--bgcolor-b3);
  padding: .1rem .6rem;
  border-radius: .4rem;

  margin-top: .4rem;
  margin-bottom: .4rem;

  padding: .5rem .6rem;

  color: var(--forecolor-f2);

}

.Card__icon_container {
  display: flex;
  flex: 1;
  justify-content: right;
  align-items: right;
  margin: 0 1rem;
  
}
.Card__icons_code {
 
  color: aliceblue;
  font-size: 1rem;


}
/*
font-size: 1.4rem;
margin-right: 1rem;
color: rgb(188, 180, 196);

*/
.Card__icons {
  text-align: right;
}



.RepoBox_icono {
  justify-content: center;
  background: rgba(73, 73, 73, 0.582);
  margin: 4px;
  padding: 4px;
  border-radius: 4px;
  width:  20pt;
  height: 20pt;
}