.SearchBox__main  {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--bgcolor-b2);
  border-radius: 8px;

  padding: 2rem 0;
}

/* ---------------------------------------------------------------------- */
/* ---------------------------------------------------------------------- */
/* ---------------------------------------------------------------------- */
/* -------------------------- SEACH_BOX_ICON ---------------------------- */

.SearchBox__LogoAndr7st {
  height: 120px;
  pointer-events: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}    
@media (prefers-reduced-motion: no-preference) {
  .SearchBox__LogoAndr7st {
    animation: App-logo-spin infinite 50s linear;
  }
}

/* ---------------------------------------------------------------------- */
/* ---------------------------------------------------------------------- */
/* ---------------------------------------------------------------------- */

.SearchBox__Andr7st_name {
  color: azure;
  font-size: 1rem;
}


.SearchBox__search_label > input {
  width: 10rem;
}

.SearchBox__search_label > button {
  margin-left: .1rem;
}
